import React, { Fragment } from "react";
import "./styles/Contact.scss";
import reactLogo from "../assets/reactLogo.svg";
import htmlLogo from "../assets/htmlLogo.svg";
import sassLogo from "../assets/sassLogo.svg";
import jestLogo from "../assets/jestLogo.svg";
import mongoLogo from "../assets/mongoLogo.svg";
import expressLogo from "../assets/expressLogo.svg";
import nodeLogo from "../assets/nodeLogo.svg";
import jsLogo from "../assets/jsLogo.svg";
import cssLogo from "../assets/cssLogo.png";
import { GoMarkGithub } from "react-icons/go";
import {
  AiFillLinkedin,
  AiFillInstagram,
  AiFillFacebook,
  AiFillMediumSquare,
} from "react-icons/ai";

const Contact = () => {
  return (
    <Fragment>
      <h1 className="wrapper">Contact</h1>
      <section className="contact-section">
        <div className="contact__description">
          <h1 className="contact__description-item">My skill set</h1>
          <ul>
            <li>
              <img src={htmlLogo} alt="" />
            </li>
            <li>
              <img src={cssLogo} alt="" />
            </li>
            <li>
              <img src={sassLogo} alt="" />
            </li>
            <li>
              <img src={jsLogo} alt="" />
            </li>
            <li>
              <img src={mongoLogo} alt="" />
            </li>
            <li>
              <img src={nodeLogo} alt="" />
            </li>
            <li>
              <img className="express" src={expressLogo} alt="" />
            </li>
            <li>
              <img src={jestLogo} alt="" />
            </li>
            <li>
              <img src={reactLogo} alt="" />
            </li>
          </ul>
        </div>
        <div className="contact__description-social">
          <h1 className="contact__description-item">Social Networks</h1>
          <ul>
            <li>
              <a href="https://github.com/jfdenis" className="social-icon">
                <GoMarkGithub fontSize="70px" />
              </a>
            </li>
            <li>
              <a
                href="https://linkedin.com/in/julio-denis-763721181"
                className="social-icon"
              >
                <AiFillLinkedin fontSize="70px" />
              </a>
            </li>
            <li>
              <a
                href="https://www.instagram.com/juliodenz/"
                className="social-icon"
              >
                <AiFillInstagram fontSize="70px" />
              </a>
            </li>
            <li>
              <a
                href="https://www.facebook.com/Juliodenisz"
                className="social-icon"
              >
                <AiFillFacebook fontSize="70px" />
              </a>
            </li>
            <li>
              <a href="https://medium.com/@julio_denis" className="social-icon">
                <AiFillMediumSquare fontSize="70px" />
              </a>
            </li>
          </ul>
        </div>
      </section>
    </Fragment>
  );
};

export default Contact;
